'use client';

import Image from 'next/image';
import Link from "next/link";
import { useState, useEffect, useRef } from "react";
import { usePathname } from 'next/navigation';
import { useTotalItems } from '@/lib/stores/cart-store';

const NAV_LINKS = [
  { name: 'Men', href: '/collections/men' },
  { name: 'Women', href: '/collections/women' },
  { name: 'Shop', href: '/shop' },
  { name: 'Impact', href: '/impact' },
  { name: 'App', href: '/app' },
];

export default function Navbar() {
  const [scrolled, setScrolled] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const totalItems = useTotalItems();
  const prevItemsRef = useRef(totalItems);
  const [badgePulse, setBadgePulse] = useState(false);
  const pathname = usePathname();

  // Scroll state for backdrop blur
  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Pulse the cart badge when items change (increase)
  useEffect(() => {
    if (totalItems > prevItemsRef.current) {
      // eslint-disable-next-line react-hooks/set-state-in-effect
      setBadgePulse(true);
      const timer = setTimeout(() => setBadgePulse(false), 300);
      return () => clearTimeout(timer);
    }
    prevItemsRef.current = totalItems;
  }, [totalItems]);

  // Body scroll lock when mobile menu opens
  useEffect(() => {
    if (mobileMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [mobileMenuOpen]);

  // Close mobile menu on Escape key
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && mobileMenuOpen) {
        setMobileMenuOpen(false);
      }
    };
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [mobileMenuOpen]);

  // Check if link is active
  const isActive = (href: string) => {
    if (href === '/shop') {
      return pathname === '/shop';
    }
    return pathname === href || pathname.startsWith(href + '/');
  };

  // Close menu and navigate
  const handleMobileNavClick = () => {
    setMobileMenuOpen(false);
  };

  // Hide navbar on admin routes — admin has its own layout
  if (pathname?.startsWith('/admin')) return null;

  return (
    <>
      <header className={`fixed top-0 left-0 right-0 z-50 transition-all duration-500 ${
        scrolled ? 'bg-black/95 backdrop-blur-xl' : 'bg-black/80 backdrop-blur-sm'
      } border-b border-white/5`}>
        <nav className="max-w-7xl mx-auto px-6 py-6 flex items-center justify-between">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-3">
            {/* Fix #5: Navbar logos are above-fold on every page */}
            <Image
              src="/diamond-white.png"
              alt=""
              width={28}
              height={28}
              priority
              className="h-7 w-auto"
            />
            <Image
              src="/dymnds-only-white.png"
              alt="DYMNDS"
              width={100}
              height={20}
              priority
              className="h-5 w-auto"
            />
          </Link>

          {/* Desktop Nav */}
          <div className="hidden md:flex items-center gap-8 text-xs tracking-[0.2em] uppercase">
            {NAV_LINKS.map((link) => (
              <Link
                key={link.href}
                href={link.href}
                className={`link-underline transition-all duration-300 ${
                  isActive(link.href) ? 'opacity-60' : 'hover:text-white/60'
                }`}
              >
                {link.name}
              </Link>
            ))}
            <Link
              href="/cart"
              className={`btn-premium px-5 py-2.5 bg-white text-black hover:bg-white/90 transition-all flex items-center gap-2 ${
                isActive('/cart') ? 'opacity-60' : ''
              }`}
            >
              Checkout
              {totalItems > 0 && (
                <span
                  className={`bg-black text-white text-xs w-5 h-5 rounded-full flex items-center justify-center font-semibold ${
                    badgePulse ? 'animate-cart-pulse' : ''
                  }`}
                  aria-live="polite"
                  aria-label={`${totalItems} items in cart`}
                >
                  {totalItems}
                </span>
              )}
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            className="md:hidden flex flex-col gap-1.5 p-2"
            aria-label={mobileMenuOpen ? 'Close menu' : 'Open menu'}
            aria-expanded={mobileMenuOpen}
          >
            <span
              className={`w-6 h-0.5 bg-white transition-all duration-300 ${
                mobileMenuOpen ? 'rotate-45 translate-y-2' : ''
              }`}
            />
            <span
              className={`w-6 h-0.5 bg-white transition-all duration-300 ${
                mobileMenuOpen ? 'opacity-0' : ''
              }`}
            />
            <span
              className={`w-6 h-0.5 bg-white transition-all duration-300 ${
                mobileMenuOpen ? '-rotate-45 -translate-y-2' : ''
              }`}
            />
          </button>
        </nav>

        {/* Mobile Menu - Full Screen Overlay */}
        {mobileMenuOpen && (
          <>
            {/* Backdrop */}
            <div
              className="modal-backdrop fixed inset-0 bg-black/98 backdrop-blur-xl z-[59]"
              style={{
                top: '72px',
                animation: 'fadeIn 400ms cubic-bezier(0.16, 1, 0.3, 1)',
              }}
              onClick={() => setMobileMenuOpen(false)}
            />

            {/* Menu Content */}
            <div
              className="fixed inset-0 z-[60] flex flex-col items-center justify-center pt-24 pb-24 safe-area-padding"
              style={{ top: '72px' }}
              role="navigation"
              aria-label="Mobile navigation"
            >
              {/* Close Button */}
              <button
                onClick={() => setMobileMenuOpen(false)}
                className="absolute top-6 right-6 w-11 h-11 flex items-center justify-center hover:opacity-60 transition-opacity duration-300"
                aria-label="Close menu"
              >
                <svg
                  className="w-6 h-6 transition-transform duration-300"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              </button>

              {/* Menu Links */}
              <div className="flex flex-col items-center gap-5">
                {NAV_LINKS.map((link, idx) => (
                  <Link
                    key={link.href}
                    href={link.href}
                    onClick={handleMobileNavClick}
                    className={`font-bebas text-4xl md:text-5xl tracking-wider uppercase py-5 px-6 link-underline transition-all duration-300 flex items-center gap-3 ${
                      isActive(link.href) ? 'opacity-60' : 'hover:opacity-60'
                    }`}
                    style={{
                      animation: `fadeInUp 600ms cubic-bezier(0.16, 1, 0.3, 1) ${idx * 75}ms backwards`,
                    }}
                  >
                    {isActive(link.href) && <Image src="/diamond-white.png" alt="" width={12} height={12} className="h-3 w-auto inline-block mr-1" />}
                    {link.name}
                  </Link>
                ))}

                {/* Cart Link at Bottom */}
                <Link
                  href="/cart"
                  onClick={handleMobileNavClick}
                  className={`font-bebas text-4xl md:text-5xl tracking-wider uppercase py-5 px-6 link-underline transition-all duration-300 flex items-center gap-3 mt-8 border-t border-white/10 pt-8 ${
                    isActive('/cart') ? 'opacity-60' : 'hover:opacity-60'
                  }`}
                  style={{
                    animation: `fadeInUp 600ms cubic-bezier(0.16, 1, 0.3, 1) ${NAV_LINKS.length * 75}ms backwards`,
                  }}
                >
                  {isActive('/cart') && <Image src="/diamond-white.png" alt="" width={12} height={12} className="h-3 w-auto inline-block mr-1" />}
                  Checkout
                  {totalItems > 0 && (
                    <span className="bg-white text-black text-sm w-6 h-6 rounded-full flex items-center justify-center font-semibold ml-2">
                      {totalItems}
                    </span>
                  )}
                </Link>
              </div>
            </div>
          </>
        )}
      </header>

      {/* Fix #7: Duplicate animation styles removed — already defined in globals.css */}
    </>
  );
}
